$selection-color: rgba(245,215,110,0.5);
$theme-color: #5dc2af;
$duration-color: #5dacce;
$duration-color-dark: #4679a7;
$duedate-color: rgba(230, 94, 77, 1);
$duedate-parent-color: rgba(230, 94, 77, 0.5);

label{
    font-weight: 300;
}


.navigation{
    button{
        background: none;
        border: none;
    }
    a {
        text-decoration: none;
        color: #4d4d4d;
        margin: 0em 0.5em;
    }
    .active{
        color: $theme-color;
    }
}
.card{
    margin: 0.5em;
}
.panel-title{
    text-align: center;
    font-size: 1.2em;
}
.item-nav{
    margin-left: auto;
}
.item-nav>button {
    background: none;
    border: none;
    font-size: 1.6em;
    color: rgba(231, 76, 60, 0.3);
}
.panel-body{
    background: white;
    padding: 1em;
}
.items-wrapper>.items-card>.children .children {
    margin-left: 2em;
}
.item-card {
    margin: 0 0 0 0;
    display: flex;
    align-items: center;
}
.body-div{
    border-bottom: 1px solid #ededed;
    width: 100%;
    display: flex;
    align-items: center;
    span{
        display:table;
    }
}

.child{
    margin-left: 1.5em;
}

.updatebox, .addnewbox {
    display: flex;
    align-items: center;
    border: $selection-color solid thin;
    textarea, input{
        outline: 0px !important;
        padding: 0.3em 0.5em 0.7em 0.4em;
        resize: none;
    }
    textarea{
        width: 100%;
    }
    input{
        border-radius: 0.3em;
        background: $duration-color;
        color: white;
        font-size: 1em;
        width: 44px;
    }
    span{
        font-size: 0.85em;
        margin-right: 0.5em;
        display: inline-table;
    }
    label{
        margin: 0;
        margin-right: 0.2em;
    }
}
.addnewbox {
    margin-left: 1.9em;
}
.updatebox{
    width: 100%;
}

.toggle-div{
    width: 2em;
    div, input{
        margin: 0; // reset margins
    }
}
.toggle {
    cursor: pointer;
    text-align: center;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none !important;
}
.toggle:after {
    content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#ededed" stroke-width="3"/></svg>');
    transform: scale(0.7);
    display: block;
    margin: 0 0 0 -1em;
}
.toggle:checked:after {
    content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="-10 -18 100 135"><circle cx="50" cy="50" r="50" fill="none" stroke="#bddad5" stroke-width="3"/><path fill="#5dc2af" d="M72 25L42 71 27 56l-4 4 20 20 34-52z"/></svg>');
    transform: scale(0.7);
    display: block;
    margin: 0 0 0 -1em;
}
.bodybox{
    padding: 0.5em;
    display: inline-block;
    white-space: pre-line;
}
.selected {
    background: $selection-color;
}
.editing{
    .selected{
        background: white;
    }
    .bodybox{
        display: none;
    }
    .body-div{
        border-bottom: none;
    }
}
.done .bodybox{
    color: #d9d9d9;
    text-decoration: line-through;
}

.item-tags {
    display: flex;
    align-items: baseline;
    font-size: 0.85em;
    span {
        padding: 0.2em 0.4em;
        margin-left: 0.2em;
        border-radius: 0.3em;
        color: white;
    }
    .done {
        color: #5a5a5a;
        background: #e6e6e6;
    }
    .duration {
        background: $duration-color;
    }
    .duedate{
        background: $duedate-color;
    }
    .duedate-parent{
        background: $duedate-parent-color;
        padding: 0 0.2em !important;
        font-size: 0.9em;    }
    .total-duration{
        background:$duration-color-dark;
    }
}
